.todo-input{position:relative}.create-todo{width:calc(100% - 75px);height:3.75rem;margin:0;padding:0;padding-left:3.125rem;padding-right:1.5625rem;border:none;border-bottom:1px solid #5a189a;outline:none;font-size:1.5625rem;color:#240046}@media(max-width: 48rem){.create-todo{font-size:1.25rem}}.create-todo::placeholder{font-size:1.5625rem;font-style:italic;opacity:.5}@media(max-width: 24.375rem){.create-todo::placeholder{font-size:6vw}}.toggle-all{position:absolute;top:0.9375rem;left:1.25rem;margin:0;transform:rotate(90deg);opacity:.3;appearance:none}.toggle-all:checked{opacity:1}.toggle-all::before{content:"‚ùØ";font-size:1.5625rem;color:#240046}